@import "../../../Commons/colors";
@import "../../../Commons/breakpoints";

.cmp-floatingaction {
    display: flex;
    position: fixed;
    width: 40px;
    height: 40px;
    justify-content: center;
    top: 6%;
    left: 1%;

    transition: 0.4s;

    .action-button {
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        position: absolute;
        width: inherit;
        height: inherit;
        border-radius: 5%;
        border: 0;
        outline: 0;
        background: $imperial-red;
        font-size: 20px;
        color: $honeydrew;
        z-index: 2;
        box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.16), 0 2px 5px 0 rgba(0, 0, 0, 0.26);
        transition: all .3s;
    }

    .actions {
        position: absolute;
        list-style: none;
        z-index: 1;
        width: 40px;
        height: 40px;

        li {
            position: absolute;
            width: 40px;
            height: 40px;
            cursor: pointer;
            border-radius: 50%;
            box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.16), 0 2px 5px 0 rgba(0, 0, 0, 0.26);
            margin: 0;
            color: $honeydrew;
            transition: all .3s;
            transform: scale(.3);
            font-size: 18px;

            a {
                width: inherit;
                height: inherit;
                display: flex;
                justify-content: center;
                align-items: center;
            }

            &:nth-child(1), &:nth-child(2) {
                background: $richBlack;
            }

            &:active {
                box-shadow: 0 6px 20px 0 rgba(0, 0, 0, 0.19), 0 8px 17px 0 rgba(0, 0, 0, 0.2);
            }
        }
    }

    .action-button {
        &.active {
            box-shadow: 0 17px 50px 0 rgba(0, 0, 0, 0.19), 0 12px 15px 0 rgba(0, 0, 0, 0.24);
        }

        &.active ~ .actions {

            li {
                transition: all .5s;
                transform: scale(1);

                &:nth-child(1) {
                    margin-top: 60px;
                }

                &:nth-child(2) {
                    margin-left: 60px;
                }
            }
        }
    }

    .action-button .cmp-text-icon {
        transition: all .5s;
    }

    .action-button {
        &.active:not(.no-rotate) .cmp-text-icon {
            transform: scale(1.1) rotate(-360deg);
            color: $prussian-blue;
        }
    }
}
